.App, .dashboard {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
  }
  button{
    padding: 8px 10px;
  }
  .navigation{
    display: flex;
    gap: 3em;
    align-items: center;
    width: 100%;
    height: 50px;
    background-color: #f5f5f5;
    padding: 0 20px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 10px;
  }
  .navigation li{
    list-style-type: none;
  }
  .navigation li:hover{
    cursor: pointer;
    }